/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
@import '../scss/quiz';
@import '../scss/animations';

div[data-type="quizblocks/quiz"] {
	&.is-selected {
		#quiz-blocks-quiz {
		
			&.create-quiz,
			&.select-quiz {
				opacity: 1;
			}
		}
	}

	.preloader {
		display: block;
		max-width: 50px;
		margin: 2em auto;
	}

	#quiz-blocks-quiz {

		&.create-quiz,
		&.select-quiz {
			background-color: #FEF8EE;
			border: 1px dashed #21759b;
			padding: 1.5em;
			text-align: center;
			opacity: 0.50;

			.components-select-control__input {
				max-width: 100%;
			}
		}

		&.create-quiz p {
			margin-bottom: 0;
		}
	}
}